@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');

body {
  background-color: black;
  margin: 1rem;
}

.App {
  height: 100vh;
  font-family: monospace;
}

pre {
  margin: 0;
  font-size: 2rem;
  font-family: 'Inconsolata', monospace;
}

.password-wrapper {
  height: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 2rem;
  width: 26ch;
}

.password {
  background-color: transparent;
  outline: none;
  border: none;
  color: #39ff14;
  font-size: 2rem;
  font-family: 'Inconsolata', monospace;
  width: fit-content;
  caret-color: black;
}

p {
  margin: 0;
  color: #39ff14;
  font-size: 2rem;
  font-family: 'Inconsolata', monospace;
}

.caret {
  width: 2rem;
  height: 2rem;
  background-color: #39ff14;
  animation: blink 750ms steps(1, end) infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.TerminalInput-wrapper {
  height: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 2rem;
  width: 26ch;
}

.TerminalInput {
  background-color: transparent;
  outline: none;
  border: none;
  color: #39ff14;
  font-size: 2rem;
  font-family: 'Inconsolata', monospace;
  width: fit-content;
  caret-color: black;
  padding: 0;
}

.caret {
  width: 1rem;
  height: 2rem;
  background-color: #39ff14;
  animation: blink 750ms steps(1, end) infinite;
}